.header{display:flex;align-items:center;justify-content:space-between;width:350px}.header input{position:relative;appearance:none;-webkit-appearance:none;width:25px;height:25px;background-color:rgba(0,0,0,0);border:2px solid #ddd;outline:none;border-radius:5px;margin-left:10px;cursor:pointer}.header input::before{position:absolute;top:50%;left:50%;width:25px;height:25px;transform:translate(-50%, -50%);content:"✓";font-size:17px;text-align:center;color:#fff}.header input:checked::before{background-color:#000;border-color:#000;border-radius:5px}.header h3{font-weight:normal}.header button{background-color:#fff;border:none;cursor:pointer}.header button .active{transform:rotate(180deg)}