.content{width:300px}.content .fruitList{display:none}.content .fruitList.active{display:block}.content .fruitList li{display:flex;justify-content:space-between;align-items:center;padding:25px 0;border-bottom:1px solid #dadada}.content .fruitList li h4{margin-left:47px;font-weight:normal}.content .fruitList li input{position:absolute;appearance:none;-webkit-appearance:none;width:25px;height:25px;background-color:rgba(0,0,0,0);border:2px solid #ddd;outline:none;border-radius:5px;margin-left:10px;cursor:pointer}.content .fruitList li input::before{position:absolute;top:50%;left:50%;width:25px;height:25px;transform:translate(-50%, -50%);content:"✓";font-size:17px;text-align:center;color:#fff}.content .fruitList li input:checked::before{background-color:#000;border-color:#000;border-radius:5px}.content .fruitList li span{margin-right:30px;max-width:120px;word-wrap:break-word;font-weight:bold}.content .priceList{list-style:none;display:flex;flex-direction:column;align-items:center;justify-content:center;margin-top:70px}.content .priceList li{display:flex;flex-direction:column;justify-content:center;align-items:start;margin:5px 0;width:350px}.content .priceList li h4{font-weight:normal;margin-bottom:15px}.content .priceList li input{margin-bottom:15px;width:100%;padding:10px;border-radius:10px;border:1px solid #e1e4e1}.content .priceList li input:focus{outline:none}.content .priceList li button{background-color:#18181b;border:none;color:#fff;padding:10px 20px;border-radius:7px;cursor:pointer}.content .priceList li button:active{background-color:#3a3a3b;box-shadow:inset 0px 0px 2px 1px #fff}